import { useEffect, useRef, useState } from 'react';
import "./works.scss"

export default function Works() {

    const [currentSlide,setCurrentSlide] = useState(0);

    const data = [
        {
            id:"1",
            icon: "assets/caddo_66-2.png",
            title: "Frontend Developer",
            desc:
            "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Nihil, maxime deserunt doloribus repudiandae labore ab?",
            img: "/assets/untitled-4-4.png"
        },
        {
            id:"2",
            icon: "assets/levilogocheap.png",
            title: "Designer",
            desc:
            "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Nihil, maxime deserunt doloribus repudiandae labore ab?",
            img: "/assets/levilogocheap.png"
        },
        {
            id:"3",
            icon: "assets/LogoStickerDemo0.png",
            title: "Graphic Engineer",
            desc:
            "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Nihil, maxime deserunt doloribus repudiandae labore ab?",
            img: "/assets/LogoStickerDemo0.png"
        },
    ];

    const handleClick = (way)=> {
        way === "left" ? setCurrentSlide(currentSlide > 0 ? currentSlide-1 : 2) :
        setCurrentSlide(currentSlide<data.length -1 ? currentSlide+1 : 0);
    }

    return (
        <div className="works" id="works">
           
            <div className="slider" style={{transform:`translateX(-${currentSlide * 100}vw)`}}>
                    {data.map((d) => (
                    <div className="container">
                            <div className="item">
                                <div className="left">
                                    <div className="leftContainer">
                                        <div className="imgContainer">
                                            <img src={d.icon} alt=""/>
                                        </div>
                                        <h2>{d.title}</h2>
                                        <p>
                                            {d.desc}
                                        </p>
                                        <span>Projects</span>
                                    </div>

                                </div>
                                <div className="right">
                                    <img src={d.img} alt=""/>
                                </div>
                            </div>
                        
                    </div>    
                    ))}
            </div>
            {/* TODO: Change these to free left and right arrow transparent pngs */}
            <img src="assets/todoCheap.png" className="arrow left" alt="" onClick={()=>handleClick("left")}/>
            <img src="assets/todoCheap.png" className="arrow right" alt="" onClick={()=>handleClick("right")}/>
        </div>
    );
}
